.box.style1 .image {
    display: block;
    position: absolute;
    top: 0;
    width: 50%;
    height: 100%;
    background: #152E33;
    overflow: hidden;
}

.box.style1 .image img {
    position: absolute;
    top: 0;
    height: 100%;
    width: auto;
}

.box.style1 .inner > :last-child {
    margin: 0;
}

.box.style1.right .image {
    left: 0;
}

.box.style1.right .image img {
    right: 0;
}

.box.style1.right .inner {
    margin-left: 50%;
    padding-left: 3em;
}

.box.style1.left .image {
    right: 0;
}

.box.style1.left .image img {
    left: 0;
}

.box.style1.left .inner {
    margin-right: 50%;
    padding-right: 3em;
}

.box.style2 {
    text-align: center;
}

.box.style2 header {
    display: inline-block;
    background: #fff;
    padding: 2em 3em 2em 3em;
    margin: 0;
}

.box.style2 header p {
    padding: 0;
}

.box.style2 .inner {
    position: relative;
    padding: 40px 0 0px 0;
}

.box.style2 .inner:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 50%;
    height: 100%;
    border-left: solid 1px #fff;
}

.box.style2 .inner .row {
    position: relative;
}

.box.style2 .inner .row:before {
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    left: 20px;
    width: calc(100% - 40px);
    border-bottom: solid 1px #fff;
}

.box.style2 .inner .image {
    position: relative;
    z-index: 1;
    padding: 20px;
}

.box.style3 {
    background: #fff;
    padding: 3em;
}

.box.style3 header {
    text-align: center;
}

/* Icons */

.icon {
    text-decoration: none;
}

.icon:before {
    display: inline-block;
    font-family: FontAwesome;
    font-size: 1.25em;
    text-decoration: none;
    font-style: normal;
    font-weight: normal;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.icon > .label {
    display: none;
}

/* Header */

#header {
    position: relative;
    color: #fff;
    text-shadow: 0 0 0.5px rgba(255, 255, 255, 0.25);
    text-align: center;
    margin: 0;
    padding: 8em 0 14em 0;
    cursor: default;
}

#header header {
    display: inline-block;
    padding: 0 0 4.5em 0;
}

#header header h1 {
    font-weight: 600;
    font-size: 2em;
    letter-spacing: 10px;
}

#header header p {
    border-top: solid 1px rgba(255, 255, 255, 0.5);
    color: rgba(255, 255, 255, 0.75);
    text-shadow: 0 0 0.5px rgba(255, 255, 255, 0.1875);
    font-size: 1em;
    text-transform: uppercase;
    letter-spacing: 3px;
    padding: 0;
    margin-top: 0.35em;
}

#header header p:before {
    content: '';
    display: block;
    border-top: solid 1px rgba(255, 255, 255, 0.5);
    margin: 4px 0 0.8em 0;
}

#header footer {
    position: absolute;
    bottom: 9em;
    margin: 0;
    width: 100%;
}

#header footer:after {
    content: '';
    display: block;
    position: absolute;
    bottom: -9em;
    left: 50%;
    height: 9em;
    border-left: solid 1px #fff;
}

#header footer:before {
    content: '';
    display: block;
    width: 90px;
    height: 66px;
    position: absolute;
    left: 50%;
    bottom: -4.5em;
    margin-left: -45px;
    margin-bottom: -33px;
    background: url("images/arrow.svg") no-repeat;
}

/* Banner */

#banner {
    position: relative;
    color: #fff;
    text-shadow: 0 0 0.5px rgba(255, 255, 255, 0.25);
    text-align: center;
    background: #35b88f url("images/banner.svg") bottom center no-repeat;
    padding: 5em 0 5em 0;
    margin: 0;
    background-size: 125% auto;
}

#banner .button {
    color: #35b882;
    text-shadow: 0 0 0.5px rgba(53, 184, 130, 0.25);
}

#banner header {
    margin: 0 0 2em 0;
}

#banner header h2 {
    font-weight: 400;
    font-size: 1.75em;
    letter-spacing: 8px;
}

#banner p {
    margin: 0;
}

#banner footer {
    margin: 2em 0 0 0;
}

#banner a {
    color: inherit;
    border-bottom-color: rgba(255, 255, 255, 0.5);
}

#banner a:hover {
    border-bottom-color: rgba(255, 255, 255, 0);
}

/* Footer */

#footer {
    position: relative;
    color: #fff;
    text-shadow: 0 0 0.5px rgba(255, 255, 255, 0.25);
    text-align: center;
    margin: 4.5em 0 0 0;
}

#footer:after {
    content: '';
    display: block;
    position: absolute;
    top: -4.5em;
    left: 50%;
    height: 4.5em;
    border-left: solid 1px #fff;
}

#footer a {
    color: #fff;
    text-shadow: 0 0 0.5px rgba(255, 255, 255, 0.25);
    border-bottom-color: rgba(255, 255, 255, 0.5);
}

#footer a:hover {
    color: #35b88f;
    text-shadow: 0 0 0.5px rgba(69, 200, 159, 0.25);
    border-bottom-color: rgba(53, 184, 143, 0.5);
}

#footer .icons {
    padding: 0.75em 2em 0.75em 2em;
    border-radius: 3em;
    border: solid 1px #fff;
    display: inline-block;
    margin: 0 0 3em 0;
}

#footer .copyright {
    margin: 0 0 9em 0;
    font-size: 0.8em;
}

/* Poptrox */

.poptrox-popup {
    -webkit-box-sizing: content-box;
    -ms-box-sizing: content-box;
    box-sizing: content-box;
    background: #fff;
    padding-bottom: 3em;
}

.poptrox-popup .loader {
    width: 48px;
    height: 48px;
    background: url("images/loader.gif");
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -24px 0 0 -24px;
}

.poptrox-popup .caption {
    position: absolute;
    bottom: 0;
    left: 0;
    background: #fff;
    width: 100%;
    height: 3em;
    line-height: 3em;
    text-align: center;
    cursor: default;
    z-index: 1;
}

.poptrox-popup .nav-next,
.poptrox-popup .nav-previous {
    -webkit-transition: opacity 0.5s ease-in-out;
    -o-transition: opacity 0.5s ease-in-out;
    transition: opacity 0.5s ease-in-out;
    position: absolute;
    top: 0;
    width: 50%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
    background: rgba(0, 0, 0, 0.01);
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

.poptrox-popup .nav-next:before,
.poptrox-popup .nav-previous:before {
    content: '';
    position: absolute;
    width: 96px;
    height: 64px;
    background: url("images/poptrox-nav.svg");
    top: calc(50% - 1.5em);
    margin: -32px 0 0 0;
}

.poptrox-popup:hover .nav-next,
.poptrox-popup:hover .nav-previous {
    opacity: 0.5;
}

.poptrox-popup:hover .nav-next:hover,
.poptrox-popup:hover .nav-previous:hover {
    opacity: 1.0;
}

.poptrox-popup .nav-next {
    right: 0;
}

.poptrox-popup .nav-next:before {
    right: 0;
}

.poptrox-popup .nav-previous {
    left: 0;
}

.poptrox-popup .nav-previous:before {
    -webkit-transform: scaleX(-1);
    -ms-transform: scaleX(-1);
    transform: scaleX(-1);
    left: 0;
    -ms-filter: "FlipH";
    -webkit-filter: FlipH;
            filter: FlipH;
}

.poptrox-popup .closer {
    -webkit-transition: opacity 0.5s ease-in-out;
    -o-transition: opacity 0.5s ease-in-out;
    transition: opacity 0.5s ease-in-out;
    position: absolute;
    top: 0;
    right: 0;
    width: 64px;
    height: 64px;
    text-indent: -9999px;
    z-index: 2;
    opacity: 0;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

.poptrox-popup .closer:before {
    -webkit-transition: background-color 0.5s ease-in-out;
    -o-transition: background-color 0.5s ease-in-out;
    transition: background-color 0.5s ease-in-out;
    content: '';
    display: block;
    position: absolute;
    right: 16px;
    top: 16px;
    width: 40px;
    height: 40px;
    border-radius: 100%;
    -webkit-box-shadow: inset 0 0 0 1px #fff;
            box-shadow: inset 0 0 0 1px #fff;
    background: rgba(255, 255, 255, 0.1) url("images/poptrox-closer.svg") center center;
    color: #fff !important;
}

.poptrox-popup:hover .closer {
    opacity: 0.5;
}

.poptrox-popup:hover .closer:hover {
    opacity: 1.0;
}

/* Wide */

@media screen and (max-width: 1680px) {

    /* Basic */
    body, input, textarea, select {
        font-size: 16pt;
    }

}

/* Normal */

@media screen and (max-width: 1080px) {

    /* Banner */
    #banner {
        padding: 4em 0 4em 0;
        background-size: auto, 175% auto;
    }

}

/* Narrow */

@media screen and (max-width: 840px) {

    /* Basic */
    body {
        background-color: #203936;
        background-image: url("images/overlay.png"), url("../../images/bg-alt.jpg");
        background-repeat: repeat, no-repeat;
        background-size: auto, 100% auto;
        background-position: top left, top center;
        background-attachment: scroll, scroll;
        font-size: 14pt;
    }

    input, textarea, select {
        font-size: 14pt;
    }

    h1, h2, h3, h4, h5, h6 {
        line-height: 1.5em;
    }

    header p {
        margin: 0.25em 0 0 0;
        padding: 0 0 0.5em 0;
    }

    /* Box */
    .box {
        position: relative;
    }

    .box section {
        margin: 0 0 2em 0;
    }

    .box.style1 {
        padding: 2.5em 2em 2.5em 2em;
    }

    .box.style1.right .inner {
        padding-left: 2em;
    }

    .box.style1.left .inner {
        padding-right: 2em;
    }

    .box.style2 header {
        padding: 2em;
    }

    .box.style2 .inner {
        position: relative;
        padding: 30px 0 0 0;
    }

    .box.style3 {
        padding: 2.5em 2em 2.5em 2em;
    }

    /* Banner */
    #banner {
        padding: 3.5em;
        background-size: 200% auto;
    }

    #banner br {
        display: none;
    }

}

/* Mobile */

@media screen and (max-width: 736px) {

    /* Basic */
    * {
        text-shadow: none !important;
    }

    body, input, textarea, select {
        line-height: 1.5em;
        font-size: 12pt;
        letter-spacing: 0;
    }

    h2, h3, h4, h5, h6 {
        font-size: 1em;
    }

    h2 {
        font-size: 1.25em;
        letter-spacing: 4px;
    }

    /* List */
    ul.default li {
        padding-left: 0.5em;
    }

    ul.icons a {
        width: 2em;
        height: 2em;
        line-height: 2em;
    }

    ul.menu li {
        display: block;
        padding: 0;
        border: 0;
        margin: 1em 0 0 0;
    }

    ul.menu li:first-child {
        margin-top: 0;
    }

    ul.actions li {
        display: block;
        margin: 1em 0 0 0;
    }

    ul.actions li:first-child {
        margin-top: 0;
    }

    ol.default li {
        padding-left: 0.25em;
    }

    /* Button */
    input[type="button"],
    input[type="submit"],
    input[type="reset"],
    button,
    .button {
        padding: 0.75em 0 0.75em 0;
        width: 100%;
        max-width: 320px;
    }

    /* Box */
    .box {
        margin-top: 4.5em;
    }

    .box:after {
        top: -4.5em;
        height: 4.5em;
    }

    .box:before {
        width: 45px;
        height: 33px;
        top: -2.25em;
        margin-left: -22.5px;
        margin-top: -16.5px;
        background-size: 45px 33px;
    }

    .box.style1 {
        padding: 0;
        text-align: center;
    }

    .box.style1 .image {
        position: relative !important;
        left: 0 !important;
        width: 100% !important;
        height: auto !important;
    }

    .box.style1 .image img {
        position: relative;
        height: auto;
        width: 100%;
    }

    .box.style1 .inner {
        margin: 0 !important;
        padding: 2em 1.25em 2em 1.25em !important;
    }

    .box.style2 .inner .row:before {
        display: none;
    }

    .box.style2 .inner .image {
        padding: 0;
        max-width: 250px;
        margin: 0 auto 20px auto;
    }

    .box.style3 {
        padding: 2em 1.25em 2em 1.25em;
    }

    /* Header */
    #header {
        padding: 6em 0 6em 0;
    }

    #header header {
        padding-left: 2em;
        padding-right: 2em;
    }

    #header header h1 {
        font-size: 1.75em;
        letter-spacing: 6px;
    }

    #header footer {
        bottom: 4.5em;
        padding: 0 2em 0 2em;
    }

    #header footer:after {
        bottom: -4.5em;
        left: 50%;
        height: 4.5em;
    }

    #header footer:before {
        width: 45px;
        height: 33px;
        bottom: -2.25em;
        margin-left: -22.5px;
        margin-bottom: -16.5px;
        background-size: 45px 33px;
    }

    /* Banner */
    #banner {
        padding: 3em 2em 3em 2em;
        background-size: auto 150%;
    }

    #banner header {
        margin: 0 0 1em 0;
    }

    #banner header h2 {
        font-size: 1.5em;
        letter-spacing: 6px;
    }

    /* Footer */
    #footer .icons {
        padding: 0.5em 1.25em 0.5em 1.25em;
    }

    #footer .copyright {
        font-size: 1em;
        margin: 0 0 4em 0;
    }

    /* Poptrox */
    .poptrox-popup .nav-next,
    .poptrox-popup .nav-previous {
        opacity: 1.0;
    }

    .poptrox-popup .nav-next:before,
    .poptrox-popup .nav-previous:before {
        display: none;
    }

    .poptrox-popup .closer {
        opacity: 0.5;
    }

}


